import {Component , ViewChild} from '@angular/core';
import {ChildComponent} from './child.Component';
@Component({  
    selector : 'app-parent',
    // template : `          nút nhấn ở child
    // <h3>{{value}}</h3>
    // <app-child (myclick) ="changeValue($event)"><app-child>
    // `


    //  template : `        c1 nút nhấn ở parent : đơn giản
    //     <app-child #child></app-child>
    //     <button (click)="child.value = child.value + 1 "> Add for child </button>
        
    // `


    //c2: sử dụng view child
    template :`    
    <app-child></app-child>
    <button (click) = "addForChild();"> Add for child </button>
    `
})

export class ParentComponent{
  // value = 0;         nút nhấn ở child
  // changeValue(isAdd : boolean){
  //     isAdd ? this.value = this.value + 1 : this.value = this.value - 1 ;
  // }
    @ViewChild(ChildComponent)
    myChild : ChildComponent;

      addForChild(){
        this.myChild.value++;
      }
}