import { Component, OnInit } from '@angular/core';
import  {WeatherService} from './weather.service';

 
@Component({
  selector: 'app-weather-service',
  templateUrl: './weather-service.component.html',
  styleUrls: ['./weather-service.component.css'],
  providers : [WeatherService],
})
export class WeatherServiceComponent implements OnInit {
  txtcityName = '';
  cityName = '';
  temp = null; 
  isLoading =  false;
  constructor(private weatherService : WeatherService) { }

  ngOnInit() {}
  getWeather(){
    this.isLoading = true;
    this.weatherService.getTemp(this.txtcityName)
    .then(temp => {
      this.cityName = this.txtcityName
      this.temp = temp
      this.isLoading = false;
      this.txtcityName = ''; 
    })
    .catch (err => {
      alert ('Can not find your city...');
      this.isLoading =false;
      this.cityName =  '';
      this.txtcityName = ''; 
    });
  }

  getMessage(){
    if(this.isLoading){
        return 'Loading ...';
    }
   return this.cityName === '' ? 'Enter your city name' : (this.cityName + ' is now ' + this.temp)
  }
}
