import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-ng-for-arr',
  templateUrl: './ng-for-arr.component.html',
  styleUrls: ['./ng-for-arr.component.css']
})
export class NgForArrComponent implements OnInit {
  addEN = '';
  addVN = '';
  filter = 'xem-tat-ca';
  showForm = false;
  arrWords = [
    { id: 1, en: 'action', vn: 'hành động', memorized: true },
    { id: 2, en: 'actor', vn: 'diễn viên', memorized: false },
    { id: 3, en: 'activity', vn: 'hoạt động', memorized: true },
    { id: 4, en: 'active', vn: 'chủ động', memorized: true },
    { id: 5, en: 'bath', vn: 'tắm', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true }
];
  constructor() { }

  ngOnInit() {
  }
      addWords(){
        this.arrWords.unshift({
            id: this.arrWords.length +1,
            en: this.addEN,
            vn: this.addVN,
            memorized: false
        });
        this.addEN = '';
        this.addVN = '';
        this.showForm = false;
      }
      deleteWord(id : number){
         const index = this.arrWords.findIndex(word => word.id === id);
         this.arrWords.splice(index , 1)
      }
      getShowStatus(memorized : boolean){
        //logic : nếu là xem-tat-ca thì show hết giá trị trả về true thì đằng sau không thực hiện nữa,
        //nếu không phải là xem-tat-ca thì nó chạy tiếp cái tu-da-nho && memorized = true 
        // nếu không phải tu-da-nho thì chạy tiếp tu-chua-nho && !memorized  = flale
        
          const dkXemTatCa  = this.filter === 'xem-tat-ca';
          const dkTuDaNho = this.filter === 'tu-da-nho' && memorized;
          const dkTuChuaNho = this.filter === 'tu-chua-nho' && memorized;
          return dkXemTatCa ||dkTuDaNho || dkTuChuaNho  ; 
          
        }
}
